'use client';

import { Article } from '@/types/news';
import Image from 'next/image';
import { motion } from 'framer-motion';

export default function NewsCard({ article }: { article: Article }) {
  return (
    <motion.div 
      className="bg-white rounded-xl shadow-lg overflow-hidden hover:shadow-xl transition-all duration-300 group"
      whileHover={{ y: -5 }}
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.3 }}
    >
      {article.urlToImage && (
        <div className="relative h-48 w-full overflow-hidden">
          <Image
            src={article.urlToImage}
            alt={article.title}
            fill
            className="object-cover group-hover:scale-105 transition-transform duration-300"
            sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 33vw"
            priority={false}
          />
        </div>
      )}
      <div className="p-6">
        <div className="flex items-center gap-2 mb-3">
          <span className="text-xs font-medium bg-blue-50 text-blue-600 px-2.5 py-1 rounded-full">
            {article.source.name}
          </span>
          <span className="text-xs text-gray-500">
            {new Date(article.publishedAt).toLocaleDateString('en-US', {
              year: 'numeric',
              month: 'short',
              day: 'numeric'
            })}
          </span>
        </div>
        <h3 className="font-bold text-lg mb-2 line-clamp-2 hover:text-blue-600 transition-colors">
          <a href={article.url} target="_blank" rel="noopener noreferrer" className="focus:outline-none focus:ring-2 focus:ring-blue-500 rounded">
            {article.title}
          </a>
        </h3>
        <p className="text-gray-600 text-sm line-clamp-3 mb-4">
          {article.description || 'No description available'}
        </p>
        <a 
          href={article.url} 
          target="_blank" 
          rel="noopener noreferrer"
          className="inline-flex items-center text-blue-600 hover:text-blue-800 text-sm font-medium group/readmore focus:outline-none focus:ring-2 focus:ring-blue-500 rounded"
          aria-label={`Read full article: ${article.title}`}
        >
          Read more
          <svg 
            xmlns="http://www.w3.org/2000/svg" 
            className="h-4 w-4 ml-1 group-hover/readmore:translate-x-1 transition-transform"
            fill="none" 
            viewBox="0 0 24 24" 
            stroke="currentColor"
            aria-hidden="true"
          >
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
          </svg>
        </a>
      </div>
    </motion.div>
  );
}